.header {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  background-image: url(../images/main-img.jpg);
  background-size: cover;
  background-position: right;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__logo {
    width: 130px;
  }

  &__title {
    padding: 0 20px 88px;
    text-align: center;
    font-size: 36px;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 64px;
    }

    @include onDesktop {
      font-size: 80px;
    }
  }
}
